.dataviz-container {
  display: -ms-flexbox;
  display: flex;
  height: 100vh; }
  .dataviz-container .player-list {
    width: 200px;
    height: 100%;
    overflow-y: scroll; }
    .dataviz-container .player-list ul {
      list-style: none;
      margin: 0;
      padding: 0; }
      .dataviz-container .player-list ul li {
        font-size: 12px;
        line-height: 16px;
        width: 100%;
        cursor: pointer; }
        .dataviz-container .player-list ul li.selected {
          font-weight: bold; }
        .dataviz-container .player-list ul li:hover {
          background-color: #ddd; }
  .dataviz-container .players-dataviz {
    width: calc(100% - 200px);
    height: 100vh; }
    .dataviz-container .players-dataviz svg .players .player {
      mix-blend-mode: multiply; }
    .dataviz-container .players-dataviz svg .player circle {
      fill-opacity: 0.1; }
    .dataviz-container .players-dataviz svg .player path {
      fill: none;
      stroke: #eee;
      stroke-opacity: 1;
      stroke-width: 1;
      stroke-linecap: round;
      stroke-linejoin: round; }
    .dataviz-container .players-dataviz svg .player text {
      font-size: 12px;
      text-anchor: end;
      stroke: #fff;
      stroke-width: 3px;
      paint-order: stroke;
      text-transform: uppercase; }
    .dataviz-container .players-dataviz svg .player line.missing-year {
      stroke: #fff;
      stroke-opacity: 1;
      stroke-linecap: round;
      stroke-linejoin: round; }
    .dataviz-container .players-dataviz svg .player.selected circle {
      fill: #ff0082;
      fill-opacity: 1;
      stroke: #fff;
      stroke-width: 2; }
    .dataviz-container .players-dataviz svg .player.selected path {
      stroke: #ff0082;
      stroke-opacity: 1;
      stroke-width: 2; }
    .dataviz-container .players-dataviz svg .player.selected line.missing-year {
      stroke-width: 3; }
    .dataviz-container .players-dataviz svg .player.selected:last-child circle {
      fill: #2d385d; }
    .dataviz-container .players-dataviz svg .player.selected:last-child path {
      stroke: #2d385d; }
    .dataviz-container .players-dataviz svg .x-axis .tick text {
      fill: #aaa;
      font-size: 12px;
      text-anchor: MIDDLE;
      stroke: #fff;
      stroke-width: 3px;
      paint-order: stroke; }
    .dataviz-container .players-dataviz svg .x-axis .tick line {
      stroke: #ccc; }



